<template>
  <div class="f5">
    <van-nav-bar title="水费查询" />
      <van-list
        v-model="loading"
        :finished="finished"
        :immediate-check="false"
        finished-text="没有更多了"
        @load="onLoad"
        :offset="10"
      >
        <van-cell-group v-for="(item,index) in waterclist" :key="index">
          <van-cell title="房间号" :value="item.room_num" />
          <van-cell title="总金额" :value="item.allmoney"  />

          <van-cell title="时间" :value="item.meter_date" :border="false" />
            <van-divider content-position="left"  :style="{margin: '0',color:'#1989fa'}">冷水费</van-divider>
            <van-cell
                v-for="itson in item.cold_cost_detail"
                :key="itson.person_name"
                :title="itson.person_name"
                :value="itson.cold_water_money"
                :border="false"
            />
             <van-divider content-position="left"  :style="{margin: '0',color:'#1989fa'}">热水费</van-divider>     
                <van-cell
                v-for="itson in item.hot_cost_detail"
                :key="itson.person_name"
                :title="itson.person_name"
                :value="itson.hot_water_money"
                :border="false"
            />               
        </van-cell-group>
      </van-list>
  </div>
</template>

<script>
import { apiwaterlist } from "@/api/index";
export default {
  name: "waterCost",
  data() {
    return {
      loading: false,
      finished: false,
      page: 1, //请求第几页
      pageSize: 5, //每页请求的数量
      total: 0, //总共的数据条数
      waterclist: [],
    };
  },
  methods: {
    // 上拉加载
    onLoad() {
      this.page++
      this.getList();
    },
    //获取列表
    getList() {
      let params = {
        page: this.page,
        pageSize: this.pageSize
      };
      apiwaterlist(params).then(res => {
        this.loading = false;
        this.total = res.data.data.count;
        let rows = res.data.data.info;
        if (res.data.data.info == null || rows.length === 0) {
          // 加载结束
          this.finished = true;
          return;
        }
        this.waterclist = this.waterclist.concat(rows);
        this.page++;
        this.isLoading = false;
        if (this.waterclist.length >= this.total) {
          this.finished = true;
        }
      });
    }
  },
  created() {
    this.getList();
  }
};
</script>

<style>

</style>


